/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  border: 0;
  outline: 0;
  padding: 0;
  vertical-align: baseline;
  box-sizing: border-box;
}

html {
  -webkit-font-smoothing: none;
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased !important;
  overflow-x: hidden;
}

body {
  margin: 0;
  overflow-x: hidden;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

a:active {
  outline: 0;
}

figure {
  margin: 0;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

input[type=search], input[type=text], input[type=password] {
  padding: 8px;
  flex-grow: 1;
  min-width: 3em;
  border: 1px solid #949494;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
}

input[type=submit],
button[type=submit] {
  background: #f7f7f7;
  border: 1px solid #ccc;
  padding: 8px 0.625em;
  color: #32373c;
  margin-left: 0.625em;
  word-break: normal;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  cursor: pointer;
}

.wp-block-heading.is-style-md-has-underline::after {
  content: "";
  display: block;
  width: 30px;
  height: 3px;
  border-radius: 3px;
  background: linear-gradient(90deg, #c73727 0%, #e76a24 100%);
  margin: 12px 0 0;
}

.wp-block-navigation.is-style-md-has-line ul li:not(:last-child) {
  padding-right: 15px;
  border-right: 1px solid;
  line-height: 1;
}

.md-btn-main {
  font-size: 16px;
  line-height: 1.143;
  text-align: center;
  display: inline-block;
  border: 2px solid currentColor;
  cursor: pointer;
  word-break: break-word;
  -webkit-text-decoration: none;
  text-decoration: none;
  position: relative;
  transition: all 0.2s ease-in-out;
  padding: 13px 27px;
  text-transform: uppercase;
  background: rgba(0, 0, 0, 0);
  color: #181e22;
}
.md-btn-main.has-right-arrow {
  padding: 13px 40.5px 13px 24.5px;
  height: auto;
}
.md-btn-main.has-right-arrow:after, .md-btn-main.btn-link-right-arrow:after {
  content: "";
  display: inline-block;
  transform: translateY(-50%);
  position: absolute;
  top: 50%;
  right: 25px;
  width: 8px;
  height: 13px;
  -webkit-mask: url(../images/ic-arrow.svg) no-repeat 50% 50%;
  mask: url(../images/ic-arrow.svg) no-repeat 50% 50%;
  background-color: currentColor;
  -webkit-mask-size: contain;
  mask-size: contain;
  transition: right 0.2s ease-out;
  will-change: right;
}
.md-btn-main.btn-link-right-arrow:before {
  content: "";
  display: block;
  height: 2px;
  background-color: currentColor;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  line-height: 1;
  width: 0;
  right: 30px;
  transition: width 0.2s ease-out, right 0.2s ease-out;
  will-change: width;
}
.md-btn-main.has-right-arrow:hover:after {
  right: 23px;
}
.md-btn-main a {
  color: currentColor !important;
  display: inline-block;
  -webkit-text-decoration: none;
  text-decoration: none;
  padding: 3px;
}
.md-btn-main.btn-link-right-arrow {
  padding: 4px 43.5px 4px 0;
  border: 0;
}
.md-btn-main:hover {
  color: #bf3126;
}
.md-btn-main:hover.btn-link-right-arrow:before {
  width: 20px;
  right: 10px;
}
